import React from "react";
import {Link} from "react-router-dom";

class ModuleListComponent extends React.Component {
    state = {
        newModuleTitle: 'some other module',
        editingModule: {}
    }

    componentDidMount() {
        // this.props.findAllModules()
        this.props.findModuleForCourse(this.props.params.courseId)
    }

    render() {
        return (
            <div style={{paddingRight: "15px"}}>


                   <h2 className="bg-dark display-2" style={{color: "white", textAlign: "center"}}> Module List </h2>

                <ul className="list-group-item bg-dark wbdv-module-list" style={{paddingBottom: "30px", width: "100%"}}>


                    {
                        this.props.modules.map(module =>
                                <li className="list-group-item wbdv-module-item wbdv-module-item-title"
                                    style={{backgroundColor: "#f5e5de"}} key={module._id}>
                                    {
                                        this.state.editingModule._id === module._id &&
                                        <div className="input-group mb-3">
                                            <div className="input-group-prepend" id="btn3">
                                                <button className="btn btn-outline-danger"
                                                        onClick={() => this.props.deleteModule(module._id)}>
                                                    Delete
                                                </button>
                                                <button className="btn btn-outline-success" onClick={() => {
                                                    this.props.updateModule(this.state.editingModule._id, this.state.editingModule)
                                                    this.setState({editingModule: {}})
                                                }}>
                                                    Save
                                                </button>
                                            </div>
                                            <input className="form-control" aria-describedby="btn3"
                                                   style={{backgroundColor: "#f5e5de"}} onChange={(e) => {
                                                const newTitle = e.target.value
                                                this.setState(prevState => ({
                                                    editingModule: {
                                                        ...prevState.editingModule,
                                                        title: newTitle
                                                    }
                                                }))
                                            }}
                                                   value={this.state.editingModule.title}/>


                                        </div>


                                    }
                                    {
                                        this.state.editingModule._id !== module._id &&
                                        <span>
                    <i className="fa fa-pencil" onClick={() => this.setState({editingModule: module})}>
                    </i>
                    <Link to={`/editor/${this.props.params.courseId}/modules/${module._id}`}>
                        <p className="lead"> {module.title} </p>
                    </Link>
                  </span>
                                    }

                                </li>
                        )
                    }


                    <li className="list-group-item" style={{backgroundColor: "#f5e5de"}}>

                        <div className="input-group mb-3">
                            <input aria-describedby="btn2" type="text" placeholder="New Module Title"
                                   className="form-control" onChange={(event) =>
                                this.setState({
                                    newModuleTitle: event.target.value
                                })}/>
                        </div>
                        <div className="input-group-append">
                            <button id="btn2" className="btn btn-outline-dark btn-block" type="button"
                                    onClick={() => this.props.createModule(
                                        this.props.params.courseId,
                                        {
                                            title: this.state.newModuleTitle
                                        })}>
                                Add
                            </button>
                        </div>

                    </li>

                </ul>

            </div>
        )
    }
}

export default ModuleListComponent