const initialState = {
    topics: []
}

const topicReducer = (state=initialState, event) => {

    switch (event.type) {

        case "UPDATE_TOPIC":
            return {
                ...state,
                topics: state.topics.map(
                    topic => topic._id === event.updateTopic._id ?
                        state.updateTopic : topic)
            }
            break
        case "FIND_TOPIC_FOR_LESSON":
            return {
                ...state,
                topics: event.topics
            }
            break
        case "FIND_TOPIC":
            console.log(event.topics)
            return {
                ...state,
                topics: event.topics
            }
            break
        case "CREATE_TOPIC":
            return {
                topics: [
                    ...state.topics,
                    event.newTopic
                ]
            }
            break
        case "DELETE_TOPIC":
            return {
                modules: state.topics.filter(topic => topic._id !== event.topicId)
            }
            break
        default:
            return state
    }
}

export default topicReducer