import {connect} from "react-redux";
import LessonTabsComponent from "../components/LessonTabsComponent";
import LessonService from "../services/LessonService";

const stateToPropertyMapper = (state) => ({
    lessons: state.lessonReducer.lessons
})

const dispatchToPropertyMapper = (dispatch) => ({
    createLesson: (moduleId, newLesson) => {
        LessonService.createLesson(moduleId, newLesson)
            .then(actualLesson => dispatch({
                type: 'CREATE_LESSON',
                newLesson
            }))
    },
    findLessonsForModule: (moduleId) => {
        LessonService.findLessonsForModule(moduleId)
            .then(actualLessons => dispatch({
                type: "FIND_LESSONS_FOR_MODULE",
                actualLessons
            }))
    },
    deleteLesson: (moduleId, lessonId) => {
        LessonService.deleteLesson(moduleId, lessonId)
            .then(status => dispatch({
                type: "DELETE_LESSON",
                lessonId
            }))
    },

    updateLesson: (moduleId, lessonId, newLessonData) => {
        LessonService.updateLesson(moduleId, lessonId, newLessonData)
            .then(status => dispatch({
                type: "UPDATE_LESSON",
                updatedLesson: newLessonData
            }))
    }
})

const LessonTabsContainer = connect
(stateToPropertyMapper, dispatchToPropertyMapper)
(LessonTabsComponent)

export default LessonTabsContainer