import React from "react";


class TopicPillsComponent extends React.Component {

    state = {
        editingTopic: {},
        newTopicTitle: 'A Given Topic'
    }

    // componentDidMount() {
    //     this.props.findTopicForLesson(this.props.params.lessonId)
    // }
    //
    // componentDidUpdate(prevProps, prevState, snapshot) {
    //     if (prevProps.params.topicId !== this.props.params.topicId) {
    //         this.props.findTopicForLesson(this.props.params.lessonId)
    //     }
    // }

    render() {
        return (
            <div>

                <ul className="list-group list-group-flush">

                    <li className="list-group-item" style={{backgroundColor: "#f5e5de"}}>Example Topic
                        <i className="fa fa-pencil float-right">
                        </i>
                    </li>

                    {
                        this.props.topics.map(topic =>

                                <li className="list-group-item" style={{backgroundColor: "#f5e5de"}} key={topic._id}>
                                    {
                                        this.state.editingTopic._id === topic._id &&
                                        <div className="input-group mb-3">
                                            <div className="input-group-prepend" id="btn3">
                                                <button className="btn btn-outline-danger"
                                                        onClick={() => this.props.deleteTopic(topic._id)}>
                                                    Delete
                                                </button>
                                                <button className="btn btn-outline-success" onClick={() => {
                                                    this.props.updateTopic(this.state.editingTopic._id, this.state.editingTopic)
                                                    this.setState({editingTopic: {}})
                                                }}>
                                                    Save
                                                </button>
                                            </div>
                                            <input className="form-control" aria-describedby="btn3"
                                                   style={{backgroundColor: "#f5e5de"}} onChange={(e) => {
                                                const newTitle = e.target.value
                                                this.setState(prevState => ({
                                                    editingTopic: {
                                                        ...prevState.editingTopic,
                                                        title: newTitle
                                                    }
                                                }))
                                            }}
                                                   value={this.state.editingTopic.title}/>


                                        </div>


                                    }
                                    {
                                        this.state.editingTopic._id !== topic._id &&
                                        <span>
                    <i className="fa fa-pencil" onClick={() => this.setState({editingTopic: topic})}>
                    </i>
                        <p className="lead"> {topic.title} </p>
                  </span>
                                    }
                                </li>
                        )

                    }

                    <li className="list-group-item" style={{backgroundColor: "#f5e5de"}}>
                        <div className="input-group mb-3">
                            <input aria-describedby="btn2" type="text" placeholder="New Topic Title"
                                   className="form-control" onChange={(event) =>
                                this.setState({
                                    newTopicTitle: event.target.value
                                })}/>
                        </div>
                        <div className="input-group-append">
                            <button id="btn2" className="btn btn-outline-dark btn-block" type="button"
                                    onClick={() => this.props.createTopic(
                                        this.props.params.lessonId,
                                        {
                                            title: this.state.newTopicTitle
                                        })}>
                                Add
                            </button>
                        </div>

                    </li>

                </ul>


            </div>
        )
    }
}

export default TopicPillsComponent