import {connect} from "react-redux";
import TopicPillsComponent from "../components/TopicPillsComponent";
import TopicService from "../services/TopicService";

const stateToPropertyMapper = (state, ownProps) => {
    return {
        topics: state.topicReducer.topics,
        newTopicTitle: state.topicReducer.newTopicTitle,
        params: ownProps.params
    }
}

const dispatchToPropertyMapper = (dispatch) => {
    return {
        findTopicForLesson: (lessonId) => {
            TopicService.findTopicForLesson(lessonId)
                .then(topicForLesson => dispatch({
                    type: 'FIND_TOPIC_FOR_LESSON',
                    topics: topicForLesson
                }))
        },
        findTopic: (topicId) => {
            TopicService.findTopic(topicId)
                .then(topic => dispatch({
                    type: 'FIND_TOPIC',
                    topic: topic
                }))
        },
        updateTopic: (topicId, newTopic) => {
            TopicService.updateTopic(topicId, newTopic)
                .then(status => dispatch({
                    type: 'UPDATE_TOPIC',
                    updatedModule: newTopic
                }))
        },
        createTopic: (lessonId, topic) => {
            TopicService.createTopic(lessonId, topic)
                .then(newTopic => dispatch({
                    type: "CREATE_TOPIC",
                    newTopic: newTopic
                }))
        },
        deleteTopic: (topicId) => {
            TopicService.deleteTopic(topicId)
                .then(status => dispatch({
                    type: "DELETE_TOPIC",
                    topicId: topicId
                }))
        }
    }
}

const TopicPillContainer = connect
(stateToPropertyMapper, dispatchToPropertyMapper)
(TopicPillsComponent)

export default TopicPillContainer