import React from "react";
import TopicPillContainer from "../containers/TopicPillContainer";

class LessonTabsComponent extends React.Component {

    state = {
        newLessonTitle: 'New Lesson',
        editingLesson: {}
    }


    componentDidMount() {
        this.props.findLessonsForModule(this.props.params.moduleId)
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.params.moduleId !== this.props.params.moduleId) {
            this.props.findLessonsForModule(this.props.params.moduleId)
        }
    }

    render() {
        return (
            <div>
                <h2 className="display-2 bg-dark" style={{color: "white", textAlign: "center"}}>Lesson Cards</h2>


                <div className="card-columns">
                    {
                        this.props.lessons.map(lesson =>
                            <div className="card">

                                <div className="card-header bg-dark" key={lesson._id}>



                                    <i className="fa fa-2x fa-pencil-square-o float-right" style={{color: "white"}}
                                       onClick={() => this.setState({editingLesson: lesson})}>
                                    </i>


                                    {
                                        this.state.editingLesson._id === lesson._id &&
                                        <div className="input-group mb-3">

                                            <div>

                                                <i className="fa fa-2x fa-trash-o" style={{color: "white"}}
                                                   onClick={() => this.props.deleteLesson(lesson._id)}>
                                                </i>

                                                <i className="fa fa-2x fa-check-circle-o" style={{color: "white"}}
                                                   onClick={() => {
                                                       this.props.updateLesson(this.state.editingLesson._id, this.state.editingLesson)
                                                       this.setState({editingLesson: {}})
                                                   }}>
                                                </i>


                                            </div>
                                            <input className="form-control" aria-describedby="btn3"
                                                   style={{backgroundColor: "#f5e5de"}} onChange={(e) => {
                                                const newTitle = e.target.value
                                                this.setState(prevState => ({
                                                    editingLesson: {
                                                        ...prevState.editingLesson,
                                                        title: newTitle
                                                    }
                                                }))
                                            }}
                                                   value={this.state.editingLesson.title}/>


                                        </div>


                                    }

                                    {
                                        this.state.editingLesson._id !== lesson._id &&
                                        <p className="lead" style={{color: "white"}}>{lesson.title}</p>

                                    }
                                </div>

                                <TopicPillContainer
                                    lessonId={this.props.params.lessonId}
                                />

                            </div>
                        )
                    }
                </div>

                <i className="fa fa-3x fa-plus-square-o" onClick={() => this.props.createLesson(
                    this.props.params.moduleId,
                    {
                        title: 'New Lesson'
                    })}>
                </i>


            </div>
        )
    }
}

// export default LessonTabs

// TODO: move mappers, connectors to separate container file like we did for module

export default LessonTabsComponent