import React from "react";
import {Link} from "react-router-dom";
import ModuleListContainer from "../containers/ModuleListContainer";
import LessonTabsContainer from "../containers/LessonTabsContainer";
import CourseService from "../services/CourseService";


const CourseEditor = ({match}) => {

    return(
        <div>

            <h1 className="display-1 bg-dark" style={{textAlign: "center", color: "white"}}>
                <Link to="/table/courses">
                    <i className="fa fa-arrow-circle-o-left" style={{color: "white"}}/>
                </Link>
                <br/>
                Course Editor
            </h1>

            <br/>


            <div className="row">
                <div className="col-5">
                    <ModuleListContainer {...match}/>
                </div>
                <div className="col-7">
                    <LessonTabsContainer {...match}/>
                </div>
            </div>
        </div>
    )
}

export default CourseEditor